export default {
  name: "Progress",
  data() {
    return {
      caseType: this.$route.params.caseType,
      stepFirst: JSON.parse(decodeURIComponent(this.$route.params.data))
    };
  },
  computed: {},
  methods: {
    go: function(path) {
      this.$router.replace(path);
    }
  },
  created() {
    console.log(this.stepFirst);
  }
};



